import { MockMatchData } from './UsersData'; 


const piotr = MockMatchData.AllMatches.find(u => u.name === 'Piotr');
const laura = MockMatchData.AllMatches.find(u => u.name === 'Laura');
const michael = MockMatchData.AllMatches.find(u => u.name === 'Michael');
const ewa = MockMatchData.AllMatches.find(u => u.name === 'Ewa');

const olga = MockMatchData.AllSwipeUsers.find(u => u.name === 'Olga');
const mateusz = MockMatchData.AllSwipeUsers.find(u => u.name === 'Mateusz');
const kinga = MockMatchData.AllSwipeUsers.find(u => u.name === 'Kinga');
const robert = MockMatchData.AllSwipeUsers.find(u => u.name === 'Robert');


export class MockMessageData {
    static getUserImage(guid) {
        const user = MockMatchData.AllMatches.find(u => u.guid === guid) || 
                     MockMatchData.AllSwipeUsers.find(u => u.guid === guid);
        return user ? user.imageUrl : undefined;
    }

    /**
     * @returns {Array} 
     */
    static getChats() {
        const now = new Date();
        const twoDaysAgo = new Date(now.getTime() - 2 * 24 * 60 * 60 * 1000);
        const oneDayAgo = new Date(now.getTime() - 1 * 24 * 60 * 60 * 1000);
        const eightHoursAgo = new Date(now.getTime() - 8 * 60 * 60 * 1000);
        const threeHoursAgo = new Date(now.getTime() - 3 * 60 * 60 * 1000);
        const thirtyMinutesAgo = new Date(now.getTime() - 30 * 60 * 1000);
        const twelveHoursAgo = new Date(now.getTime() - 12 * 60 * 60 * 1000); 
        const sixHoursAgo = new Date(now.getTime() - 6 * 60 * 60 * 1000); 
        const twoMinutesAgo = new Date(now.getTime() - 2 * 60 * 1000);

        return [
            {
                matchGuid: piotr.guid,
                partnerGuid: kinga.guid,
                messages: [
                    {
                        senderId: kinga.guid,
                        senderImage: MockMessageData.getUserImage(kinga.guid),
                        text: "Cze Piotr! Widz, e zajmujesz si Node.js. Ja chciaabym pisa testy E2E w Selenium do aplikacji backendowych. Moe masz jaki projekt, na ktrym mogabym powiczy?",
                        timestamp: twoDaysAgo.toISOString(),
                    },
                    {
                        senderId: piotr.guid,
                        senderImage: MockMessageData.getUserImage(piotr.guid),
                        text: "Hej Kinga! wietny pomys! Wanie pracuj nad mikroserwisem patnoci. W sam raz, eby wdroy testy regresji. Podrzuc Ci repo i moemy si umwi na 'test session'.",
                        timestamp: oneDayAgo.toISOString(),
                    },
                    {
                        senderId: kinga.guid,
                        senderImage: MockMessageData.getUserImage(kinga.guid),
                        text: "Super! A jakich narzdzi uywasz do testw jednostkowych? Chtnie si podszkol w kontekcie backendu.",
                        timestamp: eightHoursAgo.toISOString(),
                    },
                    {
                        senderId: piotr.guid,
                        senderImage: MockMessageData.getUserImage(piotr.guid),
                        text: "Gwnie Mocha i Chai. Zaczynajmy od E2E, a potem moemy przej do jednostkowych.",
                        timestamp: twoMinutesAgo.toISOString(),
                    },
                ],
            },
            {
                matchGuid: piotr.guid,
                partnerGuid: mateusz.guid,
                messages: [
                    {
                        senderId: mateusz.guid,
                        senderImage: MockMessageData.getUserImage(mateusz.guid),
                        text: "Cze Piotr, potrzebuj wdroy ma baz danych na AWS RDS. Czy moesz mi poleci jakie dobre tutoriale o Terraform do zarzdzania infrastruktur?",
                        timestamp: oneDayAgo.toISOString(),
                    },
                    {
                        senderId: piotr.guid,
                        senderImage: MockMessageData.getUserImage(piotr.guid),
                        text: "Jasne Mateusz! Terraform jest wietny do IaC. Zacznij od 'HashiCorp Learn'. Chtnie te pomog Ci z konfiguracj RDS i ustawieniem IAM roles, eby byo bezpiecznie.",
                        timestamp: threeHoursAgo.toISOString(),
                    },
                    {
                        senderId: mateusz.guid,
                        senderImage: MockMessageData.getUserImage(mateusz.guid),
                        text: "Brzmi jak plan! Dziki za cynk. Czy moemy spotka si w biurze w przyszym tygodniu? Przyda mi si pomoc z rolami IAM.",
                        timestamp: thirtyMinutesAgo.toISOString(),
                    },
                    {
                        senderId: piotr.guid,
                        senderImage: MockMessageData.getUserImage(piotr.guid),
                        text: "Jak najbardziej, napisz do mnie w poniedziaek, to ustalimy dzie.",
                        timestamp: twoMinutesAgo.toISOString(),
                    },
                ],
            },
            {
                matchGuid: piotr.guid,
                partnerGuid: robert.guid,
                messages: [
                    {
                        senderId: robert.guid,
                        senderImage: MockMessageData.getUserImage(robert.guid),
                        text: "Cze Piotr! Widz, e interesujesz si Gamedev. Jestem grafikiem, chtnie pomog Ci z assetami 3D lub teksturami do Unity. Potrzebujesz czego?",
                        timestamp: twelveHoursAgo.toISOString(), 
                    },
                    {
                        senderId: piotr.guid,
                        senderImage: MockMessageData.getUserImage(piotr.guid),
                        text: "Super! Aktualnie walcz z owietleniem i cieniowaniem w Unity. Czy mgby mi poleci, jak zoptymalizowa modele, eby gra dziaaa pynniej?",
                        timestamp: sixHoursAgo.toISOString(), 
                    },
                    {
                        senderId: robert.guid,
                        senderImage: MockMessageData.getUserImage(robert.guid),
                        text: "Jasne, musimy skupi si na LODs (Level of Detail) i bake'owaniu owietlenia. Moemy to omwi na szybkim videocallu jutro wieczorem. Podrzuc Ci linki do dobrych poradnikw.",
                        timestamp: twoMinutesAgo.toISOString(),
                    },
                ],
            },
            {
                matchGuid: piotr.guid,
                partnerGuid: olga.guid,
                messages: [
                    {
                        senderId: ewa.guid,
                        senderImage: MockMessageData.getUserImage(ewa.guid),
                        text: "Witaj Piotr. Jako dietetyk, potrzebuj wiedzy o SEO. Chc, aby moja nowa strona z planami dietetycznymi bya wysoko w Google. Moemy co o tym porozmawia?",
                        timestamp: oneDayAgo.toISOString(),
                    },
                    {
                        senderId: piotr.guid,
                        senderImage: MockMessageData.getUserImage(piotr.guid),
                        text: "Dzie dobry Ewa! Oczywicie! To idealna tematyka pod content marketing. Zrbmy razem szybki audyt sw kluczowych zwizanych ze zdrowiem i diet. Masz ju gotow stron?",
                        timestamp: twelveHoursAgo.toISOString(), 
                    },
                    {
                        senderId: ewa.guid,
                        senderImage: MockMessageData.getUserImage(ewa.guid),
                        text: "Strona jest prawie gotowa. wietnie! Daj mi zna, kiedy masz czas na audyt!",
                        timestamp: twoMinutesAgo.toISOString(),
                    },
                ],
            },
        ];
    }
}