import './LoginView.css';
import Cookies from 'js-cookie';
import { Link, useNavigate } from 'react-router-dom';
import { useState } from 'react';
import { RoundedTextBox } from '../components/RoundedTextBox';
import RoundedButton from '../components/RoundedButton';
import PeopleLearn from '../../resources/images/people-learn.png';
import { MockMatchData } from './../data/UsersData';

export function LoginView() {
    document.title = "Logowanie - SkillSwap";
    const navigate = useNavigate();

    const loginFunc = () => {
         const userData = MockMatchData.getByGuid('c1r1s1-m4n-4a7b-8c9d-0e1f2a3b4c5d');

        Cookies.set('skillswap-user', JSON.stringify(userData), {
            expires: 365, 
            sameSite: 'strict'
        });

        navigate('/app'); 
        window.location.reload();
    };

    return (
        <div className="login-view">
            <div className="login-overlay">

                <div className="login-left">
                    <Link to='/'>
                        <img 
                            src={PeopleLearn} 
                            alt="SkillSwap logo" 
                            className="login-icon"
                        />
                    </Link>

                    <ul className="login-features">
                        <li>Ucz si od innych  zupenie za darmo</li>
                        <li>Zdobywaj praktyczne umiejtnoci w realnym czasie</li>
                        <li>Otwarty dostp do talentw z caego wiata</li>
                        <li>Znajd partnera do nauki pod swoje cele</li>
                        <li>Ucz si na dowolnym urzdzeniu</li>
                    </ul>
                </div>

                <div className="login-right">
                    <Link to='/'><h2>SkillSwap</h2></Link>
                    <p>Zaloguj si, aby mie pene moliwoci!</p>

                    <RoundedTextBox type="email" placeholder="user@example.com"/>
                    <RoundedTextBox type="password" placeholder="********"/>

                    <RoundedButton text='Zaloguj si' onClick={loginFunc}/>

                    <span className="login-register">
                        Nie masz konta? <Link to='/shared/register'><b>Zarejestruj si</b></Link>
                    </span>
                </div>

            </div>
        </div>
    );
}

export default LoginView;