import './AboutView.css';
import { motion } from 'framer-motion';

export function AboutView() {
    document.title = "O nas - SkillSwap";

    const revealVariant = {
        hidden: { opacity: 0, y: 40, scale: 0.97 },
        visible: { opacity: 1, y: 0, scale: 1, transition: { duration: 0.8, ease: "easeOut" } }
    };

    return (
        <div className="about-view">
            <div className="about-wrapper">

                <motion.div
                    className="about-intro"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <h1>Tworzymy miejsce, w ktrym ludzie ucz si od ludzi.</h1>
                    <p>
                        Skill Swap powsta z potrzeby.  
                        Z obserwacji.  
                        Z frustracji.  
                        I z ogromnej wiary w to, e wymiana umiejtnoci moe zmienia ycie.
                    </p>
                </motion.div>

                <motion.div
                    className="about-block"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <h2>Kim jestemy?</h2>
                    <p>
                        Jestemy dwjk studentw, ktrzy  zamiast czeka a kto stworzy idealne
                        narzdzie do nauki  postanowili zrobi je sami.  
                        Bez inwestorw. Bez zaplecza. Bez gotowych schematw.
                    </p>
                </motion.div>

                <motion.div
                    className="about-highlight tech-highlight"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <p>
                        Skill Swap to nie jest aplikacja o kursach.  
                        To platforma o relacjach.
                    </p>
                </motion.div>

                <motion.div
                    className="about-block"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <h2>Nasza misja</h2>
                    <p>
                        Chcemy stworzy przestrze, w ktrej kady  niezalenie od wieku, zawodu
                        czy dowiadczenia  moe:
                        <br /><br />
                         nauczy si czego nowego,  
                         przekaza swoj wiedz dalej,  
                         zdoby realne umiejtnoci,  
                         i budowa wartociowe relacje.
                        <br /><br />
                        Wierzymy, e najwikszym zasobem nie s pienidze.  
                        S nim ludzie i to, co potrafi.
                    </p>
                </motion.div>

                <motion.div
                    className="about-block"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <h2>Dlaczego barter?</h2>
                    <p>
                        Bo nie kady ma budet na kursy.  
                        Bo nie kady chce kupowa kolejne webinary.
                        <br /><br />
                        Ale kady co potrafi.
                        <br /><br />
                        Programista moe nauczy grafika.  
                        Grafik moe nauczy montau.  
                        Montaysta moe nauczy sociali.  
                        A kto inny  jzyka, sprzeday, organizacji pracy.
                    </p>
                </motion.div>

                <motion.div
                    className="about-quote"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    Nie chodzi o to, eby mie wicej pienidzy.  
                    Chodzi o to, eby mie wicej moliwoci.
                </motion.div>

                <motion.div
                    className="about-block"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <h2>Jak widzimy przyszo?</h2>
                    <p>
                        Chcemy, aby Skill Swap sta si miejscem, do ktrego wchodzisz nie po to,
                        by tylko co przeczyta.
                        <br /><br />
                        Ale po to, by:
                        <br />
                         pozna realnego czowieka,  
                         dosta realne zadanie,  
                         zrobi realny postp,  
                         i poczu realn zmian w swoim yciu.
                    </p>
                </motion.div>

                <motion.div
                    className="about-ending"
                    variants={revealVariant}
                    initial="hidden"
                    whileInView="visible"
                    viewport={{ once: true, amount: 0.2 }}
                >
                    <h2>Nie budujemy kolejnej aplikacji.</h2>
                    <p>
                        Budujemy system wymiany umiejtnoci.  
                        Budujemy spoeczno.  
                        Budujemy przyszo nauki  razem z Wami.
                    </p>
                </motion.div>

            </div>
        </div>
    );
}

export default AboutView;